import { r as redirect, j as json } from "../../../../chunks/index.js";
const GET = async ({ request, url, locals: { supabase, getSession }, event }) => {
  const session = await getSession();
  if (!session) {
    throw redirect(303, "/");
  }
  const course = url.searchParams.get("course");
  const { data, error: error2 } = await supabase.from("assignments").select("assignment_id, title, category, due, points").eq("course_id", course);
  return json(data);
};
export {
  GET
};
