import { r as redirect, j as json } from "../../../../chunks/index.js";
const GET = async ({ request, url, locals: { supabase, getSession }, event }) => {
  const session = await getSession();
  if (!session) {
    throw redirect(303, "/");
  }
  const course = url.searchParams.get("course");
  const { data } = await supabase.from("modules").select("module_title, id, assignments ( assignment_id, title, category, in_module )").eq("course_id", course);
  return json(data);
};
export {
  GET
};
