import { r as redirect } from "../../../../../../../../../chunks/index.js";
const prerender = false;
const load = async ({ fetch, data, request, url, params, parent, locals: { supabase } }) => {
  const parentData = await parent();
  const session = parentData.session;
  if (!session) {
    throw redirect(303, "/");
  }
  const { data: assignmentDataInfo, error } = await supabase.from("assignments_info").select("description, submission_type, submission_attempts, display_as, available_start, available_end, inserted_at, assign_to").eq("assignment", params.assignment).single();
  console.log(error);
  return {
    courses: await assignmentDataInfo
  };
};
export {
  load,
  prerender
};
