import { r as redirect, j as json } from "../../../../chunks/index.js";
const GET = async ({ request, url, locals: { supabase, getSession }, event }) => {
  const session = await getSession();
  if (!session) {
    throw redirect(303, "/");
  }
  const { data, error: error2 } = await supabase.from("users").select("updated_at, username, first_name, last_name, website, avatar_url").eq("id", session.user?.id).single();
  return json({ data });
};
export {
  GET
};
