import { r as redirect, j as json } from "../../../../chunks/index.js";
const GET = async ({ request, url, locals: { supabase, getSession }, event }) => {
  const session = await getSession();
  if (!session) {
    throw redirect(303, "/");
  }
  const { data, error: error2 } = await supabase.from("courses").select("id, course_image_url, course_title, course_prefix, course_number, course_term");
  return json({ data });
};
export {
  GET
};
