const prerender = false;
const ssr = false;
const actions = {
  createModule: async ({ request, url, params, locals: { supabase } }) => {
    const formData = await request.formData();
    const { data } = await supabase.auth.refreshSession();
    let user;
    if (data == null) {
      const { data: data2 } = await supabase.auth.refreshSession();
      user = data2.user;
    }
    user = data.user;
    const name = formData.get("name");
    const course_id = params.slug;
    if (user != null) {
      const updates = {
        user_id: user.id,
        course_id,
        module_title: name
      };
      await supabase.from("modules").upsert(updates);
    }
  },
  addItemToModule: async ({ request, url, params, locals: { supabase } }) => {
    const formData = await request.formData();
    const { data } = await supabase.auth.refreshSession();
    let user;
    if (data == null) {
      const { data: data2 } = await supabase.auth.refreshSession();
      user = data2.user;
    }
    user = data.user;
    const id = formData.get("id");
    const module = formData.get("modules");
    const course_id = params.slug;
    if (user != null) {
      const updates = {
        in_module: module
      };
      const { error } = await supabase.from("assignments").update(updates).eq("creator_id", user.id).eq("course_id", course_id).eq("assignment_id", id);
      console.log(error);
    }
  }
};
export {
  actions,
  prerender,
  ssr
};
