import { r as redirect, j as json } from "../../../../../chunks/index.js";
const GET = async ({ request, url, locals: { supabase, getSession }, event }) => {
  const session = await getSession();
  if (!session) {
    throw redirect(303, "/");
  }
  const course = url.searchParams.get("course");
  const { error: error2, data: res, status } = await supabase.rpc(
    "new_enroll_code",
    { course_id: course, created_by: session.user.id }
  );
  return json({ res, error: error2, status });
};
export {
  GET
};
